import {action, computed, observable} from "mobx";
import {
    IApplicantMedia,
    IConfigData,
    IEducationBackground,
    IEmergencyContact,
    IHealthCondition,
    IPersonalDetails,
    IPersonalStatement,
    IUSerData
} from "../constants/interfaces";
import {FirebaseServices} from "../services";
import DynamicResponse from "../utils/DynamicResponse";
import {UserTypes} from "../enums";

function isValueOfFieldValid(value?: any, isStringType: boolean = true, minChar: number = 1): boolean {
    return !(value === null || value === undefined || (isStringType && value.toString().trim().length < minChar));
}

class UserStore {
    @observable loadingInitialize: boolean = false;
    @observable loading: boolean = false;
    @observable userData: IUSerData = {
        uid: localStorage.getItem('uid')?.toString(),
        email: localStorage.getItem('email')?.toString(),
        role: UserTypes.UNDEFINED,
    };
    private firebaseServices = FirebaseServices.getInstance();
    @observable private _dataConfigApplicationForm: IConfigData = {};
    @observable private _dataPersonalDetails: IPersonalDetails = {};
    @observable private _dataEducationBackground: IEducationBackground = {};
    @observable private _dataHealthCondition: IHealthCondition = {};
    @observable private _dataPersonalStatement: IPersonalStatement = {};
    @observable private _dataEmergencyContact: IEmergencyContact = {};
    @observable private _dataApplicantMedia: IApplicantMedia = {};

    get isAdmin(): boolean {
        return this.userData.role === UserTypes.ADMIN;
    }

    get configApplicationForm(): IConfigData {
        return this._dataConfigApplicationForm;
    }

    get personalDetails(): IPersonalDetails {
        return this._dataPersonalDetails;
    }

    get educationBackground(): IEducationBackground {
        return this._dataEducationBackground;
    }

    get healthCondition(): IHealthCondition {
        return this._dataHealthCondition;
    }

    get personalStatement(): IPersonalStatement {
        return this._dataPersonalStatement;
    }

    get emergencyContact(): IEmergencyContact {
        return this._dataEmergencyContact;
    }

    get applicantMedia(): IApplicantMedia {
        return this._dataApplicantMedia;
    }

    @computed
    get personalDetailsIsValid(): boolean {
        return isValueOfFieldValid(this._dataPersonalDetails.fullName) &&
            isValueOfFieldValid(this._dataPersonalDetails.address) &&
            isValueOfFieldValid(this._dataPersonalDetails.city) &&
            isValueOfFieldValid(this._dataPersonalDetails.province) &&
            isValueOfFieldValid(this._dataPersonalDetails.postcode) &&
            isValueOfFieldValid(this._dataPersonalDetails.country) &&
            isValueOfFieldValid(this._dataPersonalDetails.mobilePhone) &&
            isValueOfFieldValid(this._dataPersonalDetails.tShirtSize) &&
            isValueOfFieldValid(this._dataPersonalDetails.dateOfBirth, false) &&
            isValueOfFieldValid(this._dataPersonalDetails.sex) &&
            isValueOfFieldValid(this._dataPersonalDetails.maritalStatus) &&
            isValueOfFieldValid(this._dataPersonalDetails.passportNumber) &&
            isValueOfFieldValid(this._dataPersonalDetails.expiryDate, false) &&
            isValueOfFieldValid(this._dataPersonalDetails.nationality);
    }

    @computed
    get educationBackgroundIsValid(): boolean {
        return isValueOfFieldValid(this._dataEducationBackground.latestDegree) &&
            isValueOfFieldValid(this._dataEducationBackground.homeUniversity) &&
            isValueOfFieldValid(this._dataEducationBackground.country);
    }

    @computed
    get healthConditionIsValid(): boolean {
        return isValueOfFieldValid(this._dataHealthCondition.disability) &&
            isValueOfFieldValid(this._dataHealthCondition.dietaryRestriction);
    }

    @computed
    get personalStatementIsValid(): boolean {
        return isValueOfFieldValid(this._dataPersonalStatement.informationSources) &&
            isValueOfFieldValid(this._dataPersonalStatement.motivation) &&
            isValueOfFieldValid(this._dataPersonalStatement.personalExperiences) &&
            isValueOfFieldValid(this._dataPersonalStatement.specialitiesDescribe) &&
            isValueOfFieldValid(this._dataPersonalStatement.afterProgram);
    }

    @computed
    get emergencyContactIsValid(): boolean {
        return isValueOfFieldValid(this._dataEmergencyContact.fullName) &&
            isValueOfFieldValid(this._dataEmergencyContact.relationship) &&
            isValueOfFieldValid(this._dataEmergencyContact.email) &&
            isValueOfFieldValid(this._dataEmergencyContact.phone) &&
            isValueOfFieldValid(this._dataEmergencyContact.address) &&
            isValueOfFieldValid(this._dataEmergencyContact.city) &&
            isValueOfFieldValid(this._dataEmergencyContact.province) &&
            isValueOfFieldValid(this._dataEmergencyContact.country) &&
            isValueOfFieldValid(this._dataEmergencyContact.postcode);
    }

    @computed
    get applicantMediaIsValid(): boolean {
        return isValueOfFieldValid(this._dataApplicantMedia.passportPhoto) &&
            isValueOfFieldValid(this._dataApplicantMedia.passportScan) &&
            isValueOfFieldValid(this._dataApplicantMedia.medicalCertificate) &&
            isValueOfFieldValid(this._dataApplicantMedia.declarationLetter);
    }

    @computed
    get applicationFormIsValid(): boolean {
        return this.personalDetailsIsValid &&
            this.educationBackgroundIsValid &&
            this.healthConditionIsValid &&
            this.personalStatementIsValid &&
            this.emergencyContactIsValid &&
            this.applicantMediaIsValid;
    }

    @action
    clear() {
        this._dataPersonalDetails = {};
        this._dataEducationBackground = {};
        this._dataHealthCondition = {};
        this._dataPersonalStatement = {};
        this._dataEmergencyContact = {};
        this._dataApplicantMedia = {};
    }

    @action
    async initialize(): Promise<DynamicResponse> {
        let response = new DynamicResponse();
        this.loadingInitialize = true;
        try {
            this.userData = await this.firebaseServices.fetchUserData();

            if (this.userData.role === UserTypes.USER) {
                const result = await this.firebaseServices.fetchApplicationForm();
                this._dataConfigApplicationForm = result.configData;
                this._dataPersonalDetails = result.personalDetails;
                this._dataEducationBackground = result.educationBackground;
                this._dataHealthCondition = result.healthCondition;
                this._dataPersonalStatement = result.personalStatement;
                this._dataEmergencyContact = result.emergencyContact;
                this._dataApplicantMedia = result.applicantMedia;
            }

            response.success();
        } catch (e) {
            response.error = (e);
        }

        this.loadingInitialize = false;
        return response;
    }

    @action
    async updatePersonalDetails(personalDetails: IPersonalDetails): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updatePersonalDetails(personalDetails);
        if (!res.hasError())
            this._dataPersonalDetails = personalDetails;

        return res;
    }

    @action
    async updateEducationBackground(educationBackground: IEducationBackground): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updateEducationBackground(educationBackground);
        if (!res.hasError())
            this._dataEducationBackground = educationBackground;

        return res;
    }

    @action
    async updateHealthCondition(healthCondition: IHealthCondition): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updateHealthCondition(healthCondition);
        if (!res.hasError())
            this._dataHealthCondition = healthCondition;

        return res;
    }

    @action
    async updatePersonalStatement(personalStatement: IPersonalStatement): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updatePersonalStatement(personalStatement);
        if (!res.hasError())
            this._dataPersonalStatement = personalStatement;

        return res;
    }

    @action
    async updateEmergencyContact(emergencyContact: IEmergencyContact): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updateEmergencyContact(emergencyContact);
        if (!res.hasError())
            this._dataEmergencyContact = emergencyContact;

        return res;
    }

    @action
    async updateApplicantMedia(applicantMedia: IApplicantMedia): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updateApplicantMedia(applicantMedia);
        if (!res.hasError())
            this._dataApplicantMedia = applicantMedia;

        return res;
    }

    @action
    async updateConfigApplicationForm(configData: IConfigData): Promise<DynamicResponse> {
        const res = await this.firebaseServices?.updateConfigApplicationForm(configData);
        if (!res.hasError())
            this._dataConfigApplicationForm = configData;

        return res;
    }
}

export default UserStore;