interface IFirebaseError {
    code: String | null;
    message: String | null;
}

class DynamicResponse {
    data: any | undefined;
    error: IFirebaseError | undefined;

    constructor(data?: any, error?: IFirebaseError) {
        this.data = data;
        this.error = error;
    }

    success(): void {
        this.data = {success: true};
    }

    hasError(): boolean {
        return this.error !== undefined || this.data === undefined;
    }
}

export default DynamicResponse;