import React, {lazy, Suspense} from 'react';
import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";
import {createMuiTheme, LinearProgress, MuiThemeProvider} from "@material-ui/core";
import {APPLICATION_FORM, HOME, SIGN_IN, SIGN_UP} from "./constants";
import 'react-toastify/dist/ReactToastify.min.css';
import {ToastContainer} from "react-toastify";
import {PrivateRoute} from "./components/commons/PrivateRoute";
import UserStoreProvider from "./providers/UserProvider";
import {AuthProvider} from "./providers";
import {DETAIL_USER, LIST_USER} from "./constants/routes";

const Home = lazy(() => import('./scenes/Home'));
const ApplicationForm = lazy(() => import('./scenes/user/ApplicationForm'));
const ListUser = lazy(() => import('./scenes/admin/ListUser'));
const DetailUser = lazy(() => import('./scenes/admin/DetailUser'));
const SignIn = lazy(() => import('./scenes/SignIn'));
const SignUp = lazy(() => import('./scenes/SignUp'));

const theme = createMuiTheme({
    palette: {
        primary: {
            main: '#077a80'
        },
        secondary: {
            light: '#ffb74d',
            main: '#ff9800',
            contrastText: '#f57c00',
        },
    },
});

function App() {
    return (
        <MuiThemeProvider theme={theme}>
            <Router basename={'/umicamp/apply'}>
                <ToastContainer/>
                <Suspense fallback={<LinearProgress/>}>
                    <AuthProvider>
                        <UserStoreProvider>
                            <Switch>
                                <PrivateRoute exact path={HOME} component={Home}/>
                                <PrivateRoute exact path={APPLICATION_FORM} component={ApplicationForm}/>
                                <PrivateRoute exact path={LIST_USER} component={ListUser}/>
                                <PrivateRoute exact path={`${DETAIL_USER}:uid`} component={DetailUser}/>
                                <Route exact path={SIGN_IN} component={SignIn}/>
                                <Route exact path={SIGN_UP} component={SignUp}/>
                                <Redirect from="*" to={SIGN_IN}/>
                            </Switch>
                        </UserStoreProvider>
                    </AuthProvider>
                </Suspense>
            </Router>
        </MuiThemeProvider>
    );
}

export default App;
