import React, {useContext, useEffect} from "react";
import {FirebaseContext} from "../contexts";

const AuthProvider: React.FC = ({children}) => {
    const firebaseApp = useContext(FirebaseContext);

    useEffect(() => {
        firebaseApp?.auth.onAuthStateChanged(async (user) => {
        if (user) {
                localStorage.setItem('uid', user?.uid as string);
                localStorage.setItem('email', user?.email as string);
            } else {
                localStorage.removeItem('uid');
                localStorage.removeItem('email');
            }

            // console.log('AuthProvider::useEffect() : user?.emailVerified', user?.emailVerified);
            // console.log('AuthProvider::useEffect() : uid', localStorage.getItem('uid'));
        });
    }, [firebaseApp]);

    return (<>
        {children}
    </>);
};

export default AuthProvider;