import * as React from "react"
import {useContext} from "react"
import {Redirect, Route, RouteComponentProps, RouteProps} from "react-router-dom"
import LoadingBackground from "./LoadingBackdrop";
import {useObserver} from 'mobx-react';
import {UserStoreContext} from "../../contexts";

type RouteComponent = React.FC<RouteComponentProps<{}>> | React.ComponentClass<any>

export const PrivateRoute: React.FC<RouteProps> = ({component, ...rest}) => {
    const userStore = useContext(UserStoreContext);

    const renderFn = (Component?: RouteComponent) => (props: RouteProps) => {
        if (!Component) {
            return null
        }


        const uid = localStorage.getItem('uid');
        if (uid) {
            // @ts-ignore
            return <Component {...props} />
        }

        const redirectProps = {
            to: {
                pathname: "/sign-in",
                state: {from: props.location},
            },
        };

        return <Redirect {...redirectProps} />
    };

    return useObserver(() => {
        if (userStore?.loadingInitialize)
            return <LoadingBackground open={true}/>;

        return <Route {...rest} render={renderFn(component)}/>;
    });
};