export const URL_PRODUCTION = 'http://oia.um.ac.id/umicamp';
export const URL_DEVELOPMENT = 'http://oia.um.test/umicamp';

export const HOST_ORIGIN = window.location.origin;

const hostname = window.location.hostname; // "localhost"
export const HOST_NAME = hostname === 'localhost' || hostname === 'oia.um.test' ?
    URL_DEVELOPMENT :
    URL_PRODUCTION;

export const API = `${HOST_NAME}/api`;
export const UPLOAD_MEDIA = `${HOST_NAME}/upload-media`;
export const FOR_UPLOAD_MEDIA = '/upload/index.php';

export const CURRENT_ROUTE = `${HOST_ORIGIN}/umicamp/apply`;

export const IS_DEV_MODE: boolean = HOST_NAME === URL_DEVELOPMENT ? true : false;