import React, {useEffect} from "react";
import {useLocalStore} from "mobx-react";
import {UserStoreContext} from "../contexts";
import {UserStore} from "../stores";


const UserStoreProvider: React.FC = ({children}) => {
    const userStore = useLocalStore(() => new UserStore());

    useEffect(() => {
        const initializeUserStore = async () => {
            await userStore.initialize();
        };
        const uid = localStorage.getItem('uid')?.toString();

        if (uid) initializeUserStore();
    }, [userStore]);

    return (<UserStoreContext.Provider value={userStore}>
        {children}
    </UserStoreContext.Provider>);
};

export default UserStoreProvider;