import DynamicResponse from "./DynamicResponse";
import axios, {AxiosRequestConfig, AxiosResponse} from "axios";
import {API} from "../constants/api";


interface IEmailServiceProps {
    // from: string,
    to: string,
    subject: string,
    content: string,
}

const useEmailService = () => {
    const EMAIL_FROM: string = 'umicamp@um.ac.id';

    const sendEmail = async (props: IEmailServiceProps): Promise<DynamicResponse> => {
        let response: DynamicResponse = new DynamicResponse();

        try {
            const config: AxiosRequestConfig = {headers: {'Content-Type': 'multipart/form-data'}};

            const postData: FormData = new FormData();
            postData.append('from', EMAIL_FROM);
            postData.append('to', props.to);
            postData.append('subject', props.subject);
            postData.append('content', props.content);

            const result: AxiosResponse<any> = await axios.post(`${API}/share/index.php`, postData, config);

            if (result.data.error)
                throw  new Error(result.data.message);

            response.success();
        } catch (error) {
            console.log(error.toString());
            response.error = error.toString();
        }

        return response;
    };


    return {
        sendEmail: sendEmail,
    };
};

export default useEmailService();